<style lang=less>
    @import '../../../assets/index.less';
    @import './friend.less';

</style>
<template>
    <div class="base-info friend">
        <!-- 基本信息 -->
        <p class="title">
            <span>
            </span>
            {{$t('center.friend[0]')}}
        </p>
        <el-table :data="tableData" style="width: 100%;min-height: 580px;">
            <el-table-column prop="realname" :label="$t('center.friend[1]')" width="180">
            </el-table-column>
            <el-table-column prop="mobile" :label="$t('center.friend[2]')" width="180">
            </el-table-column>
            <el-table-column prop="reg_time" :label="$t('center.friend[3]')" align="right">
                <template slot-scope="scope">
                    {{$public.timestampToTime(scope.row.reg_time)}}
                </template>
            </el-table-column>
        </el-table>
        <pagination :page="paginationdata" @change="getValueFromChild"></pagination>
    </div>
</template>
<script>
  import  pagination from "@/components/pagination";
  import {my_down} from "@/api/user.js";
export default {
    data () {
        return {
            tableData: [],
            paginationdata:{
                total: 1,
                p:1,
                size:10
            }
        }
    },
    created () {
        this.getdata('1');
    },
    watch: {
    },
    methods: {
        getdata(page){
            let _this = this;
            my_down({p:page}).then(res=>{
                _this.tableData = res.data.data.list;
                _this.paginationdata.total = res.data.data.count;
            });
        },
        getValueFromChild(value1){
            let _this = this;
            _this.getdata(value1)
        }
    },
    components: {
        pagination,
    },
}
</script>



